.banner {
    color: white;
    object-fit: contain;
    height: 448px;
    
  }
  .banner_contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
  }
  .banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
  }
  .banner_description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 88px;
  }
  .banner_button {
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
  }
  .banner_button:hover {
    color: black;
    background-color: aliceblue;
    transition: all 0.2s;
  }
  .banner_fade {
    height: 17.4rem;
    background-image: linear-gradient(
      180deg,
      transparent,
      rgba(26, 26, 26, 0.589),
      rgb(0, 0, 0)
    );
  }
  @media(max-width:500px){
    .banner_title{
      font-size: 2em;
    }
    .banner_description{
      max-width: 300px;
    }
  }